




import { createComponent, onMounted, ref } from '@vue/composition-api'
import 'ol/ol.css'
import { initializeMap } from '../lib/map'

export default createComponent({
  setup () {
    const root = ref<HTMLElement>(null)

    onMounted(() => {
      initializeMap(root.value!)
    })

    return { root }
  }
})
